<template>
  <div class="stations" v-if="!loading">
      <table class="table">
          <thead>
              <th>ID</th>
              <th>Name</th>
              <th>Longitude</th>
              <th>Latitude</th>
              <th></th>
          </thead>
          <tbody>
              <tr v-for="station in stations" :key="station.id">
                  <td>{{station.id}}</td>
                  <td>{{station.name}}</td>
                  <td>{{station.longitude}}</td>
                  <td>{{station.latitude}}</td>
                  <td>
                      <BaseButton @click="selectedStation = JSON.parse(JSON.stringify(station)); $refs.modal.show()">
                        EDIT
                      </BaseButton>
                  </td>
              </tr>
          </tbody>
      </table>
      <vl-map :load-tiles-while-animating="true" :load-tiles-while-interacting="true" style="height: 400px" data-projection="EPSG:4326">
        <vl-view
            :zoom.sync="zoom" 
            :center.sync="center" 
            :rotation.sync="rotation">
        </vl-view>
        <vl-layer-tile id="osm">
            <vl-source-osm></vl-source-osm>
        </vl-layer-tile>
        <vl-feature v-for="station in stations" :key="station.name" :properties="station">
            <vl-geom-point :coordinates="[station.longitude,station.latitude]">
            </vl-geom-point>
            <vl-style-box>
                <vl-style-icon 
                    :src="require('@/assets/icons/place.png')"
                    :anchor="[0.5, 1]"
                />
            </vl-style-box>
        </vl-feature>
        <vl-interaction-select :features.sync="selectedFeatures"/>
    </vl-map>
    {{selectedFeatures.map(feature => {
        return `
            Station name: ${feature.properties.name},
            longitude: ${feature.properties.longitude},
            latitude: ${feature.properties.longitude},
        `
    })}}
    <Modal ref="modal" @confirm="update()" :loading="loading">
        <div class="form" v-if="selectedStation">
            <div class="item">
                <div>Name</div>
                <input type="text" placeholder="Passenger Name" v-model="selectedStation.name">
            </div>
            <div class="item">
                <div>Longitude</div>
                <input type="text" placeholder="Passenger Name" v-model="selectedStation.longitude">
            </div>
            <div class="item">
                <div>Latitude</div>
                <input type="text" placeholder="Passenger Name" v-model="selectedStation.latitude">
            </div>
        </div>
    </Modal>
  </div>
  <Spinner v-else/>
</template>

<script>
import Axios from 'axios';
export default {
    data() {
      return {
        stations: [],
        loading: false,
        zoom: 4.5,
        center: [66.9237, 48.0196],
        rotation: 0,
        selectedFeatures: [],
        selectedStation: false
      }
    },
    mounted() {
      this.loading = true;
      let _this = this;
      Axios.get("http://localhost:8080/searchTrains/mainStations/")
      .then((res) => {
        _this.stations = []
        res.data.forEach(station => {
            if (_this.stations.findIndex(x => x.name == station.name) == -1) {
                _this.stations.push(station);
            }
        });
      }).catch(() => {
          _this.$notify({
              group: 'notifications',
              title: 'Something went wrong',
              text: 'Error occured while loading tickets',
              type: 'warn'
          });
      })
      .finally(() => {
          _this.loading = false;
      });
    },
    methods: {
      
    }
}
</script>

<style lang="scss" scoped>
    .stations{
        width: 100%;
        padding: 40px 46px;
        .table{
            width: 100%;
            th{
                text-align: left;
            }
            tr{
                td:last-child{
                    text-align: right;
                }
            }
            
        }
    }
</style>