<template>
    <form class="form" @submit.prevent="submit()">
        <div class="header">Sign Up in the portal</div>
        <input v-model="email" type="email" placeholder="email address">
        <input v-model="name" type="text" placeholder="name">
        <input v-model="surname" type="text" placeholder="surname">
        <input v-model="password" type="password" placeholder="password">
        <button type="submit" class="btn">
            <template v-if="!loading">
                SIGN UP
            </template>
            <Spinner color="#fff" v-else/>
        </button>
        <button class="btn login" @click.prevent="$router.push('/sign/in')">
            GO TO LOGIN
        </button>
    </form>
</template>

<script>
import axios from 'axios';
import { saveUser } from '../../utils/auth'
export default {
    data(){
        return {
            email: '',
            name: '',
            surname: '',
            password: '',
            loading: false
        }
    },
    methods: {
        submit() {
            // let _this = this
            if(!(this.email && this.password)){
                this.$notify({
                    group: 'notifications',
                    title: 'Form is empty',
                    text: 'Please, form the field',
                    type: 'warn'
                });
                return
            }
            this.loading = true
            let _this = this
            axios.post('http://localhost:8080/users', {
                email: this.email,
                firstName: this.name,
                lastName: this.surname,
                password: this.password,
                roles: []
            }).then(function(r) {
                console.log(r);
                let user = r.data;
                saveUser(user.id, user.email, _this.password, ['ROLE_PASSENGER']);
                _this.$store.commit('setLogedIn', true);
                _this.$router.push('/profile');
                _this.$notify({
                    group: 'notifications',
                    title: 'Success',
                    text: 'Signed up'
                });
            }).catch(function() {
                _this.$notify({
                    group: 'notifications',
                    title: 'Could not sign up',
                    text: 'Please, check the form',
                    type: 'warn'
                });
            }).finally(function() {
                _this.loading = false
            });
        }
    }
}
</script>

<style lang="scss" scoped>
    .login{
        background-color: #fff !important;
        color: #fb7b4c !important;
        border: 1px solid #fb7b4c;
        border-radius: 3px;
    }
</style>