<template>
    <form class="form" @submit.prevent="submit()">
        <div class="header">Login to the portal</div>
        <input v-model="email" type="email" placeholder="email address">
        <input v-model="password" type="password" placeholder="password">
        <button type="submit" class="btn">
            <template v-if="!loading">
                LOGIN
            </template>
            <Spinner color="#fff" v-else/>
        </button>
        <button class="btn signup" @click.prevent="$router.push('/sign/up')">
            GO TO SIGNUP
        </button>
    </form>
</template>

<script>
import axios from 'axios';
import { saveUser } from '../../utils/auth';

export default {
    data(){
        return {
            email: '',
            password: '',
            loading: false
        }
    },
    methods: {
        submit() {
            if(!(this.email && this.password)){
                this.$notify({
                    group: 'notifications',
                    title: 'Form is empty',
                    text: 'Please, form the field',
                    type: 'warn'
                });
                return
            }
            let _this = this
            _this.loading = true
            axios.get('http://localhost:8080/users/user_info', {
                auth: {
                    username: _this.email,
                    password: _this.password
                }
            }).then(function (res) {
                if (res.data) {
                    let user = res.data;
                    let roles = user.roles.map(role => role.name);
                    saveUser(user.id, _this.email, _this.password, roles);
                    _this.$store.commit('setLogedIn', true)
                    _this.$router.push('/')
                } else {
                    _this.$notify({
                        group: 'notifications',
                        title: 'Error',
                        text: 'Please, provide valid credentials'
                    });
                }
            }).catch(function () {
                _this.$notify({
                    group: 'notifications',
                    title: 'Opps.. Could not sign in',
                    text: 'Please, check credentials!',
                    type: 'warn'
                });
            }).finally(function(){
                console.log("finally");
                _this.loading = false;
            });
        }
    }
}
</script>

<style lang="scss" scoped>
    .signup{
        background-color: #fff !important;
        color: #fb7b4c !important;
        border: 1px solid #fb7b4c;
        border-radius: 3px;
    }
</style>