<template>
  <div class="admin-page">
      <div class="header">
          <div class="header__item header__item--logo" @click="$router.push('/')">eStation</div>
          <div class="header__item header__item--routes" v-if="isAgent()" @click="$router.push('/admin/schedules')">Schedules</div>
          <div class="header__item header__item--routes" v-if="isAgent()" @click="$router.push('/admin/stations')">Stations</div>
          <div class="header__item header__item--routes" v-if="isAgent()" @click="$router.push('/admin/logs')">Logs</div>
          <div class="header__item header__item--tickets" @click="$router.push('/admin/tickets')">Tickets</div>
          <div class="header__item header__item--employees" v-if="isAgent()" @click="$router.push('/admin/employees')">Employees</div>
          <div class="header__item header__item--account" @click="logOut()">logout</div>
      </div>
      <router-view/>
  </div>
</template>

<script>
import {isManager, isAgent} from '../utils/auth'
import {logout} from "../utils/auth"

export default {
  mounted() {
    if (!isManager() && !isAgent()) {
      this.$notify({
          group: 'notifications',
          title: 'Permission denied',
          text: 'You are not allowed to access this page',
          type: 'warn'
      });
      this.$router.push('/');
    }
  },
  methods: {
    isAgent() {
      return isAgent();
    },
    isManager() {
      return isManager();
    },
    logOut() {
        logout();
        this.$store.commit('setLogedIn', false);
        this.$router.push('/');
    }
  }
}
</script>

