<template>
    <div class="logs">
        <div class="header">
            <transition name="fade">
                <Spinner v-show="isStatusLoading"/>
            </transition>
            <div>
                Logging is {{ enabled ? 'enabled' : 'disabled'}}
            </div>
            <label class="switch">
                <input type="checkbox" v-model="enabled" @change="setLogsStatus(enabled)">
                <span class="slider round"></span>
            </label>
        </div>
        <div class="timer_row" v-if="enabled">
            <div class="">
                updating after:
            </div>
            <div v-if="!isDataLoading">
                {{secLeft}}
            </div>
            <Spinner v-else />
        </div>
        <SortedTable :values="values" class="table" sort="dateTime" dir="desc">
            <thead>
                <tr>
                <th scope="col" style="text-align: left; width: 10rem;">
                    <SortLink name="userId">UserId</SortLink>
                </th>
                <th scope="col" style="text-align: left; width: 10rem;">
                    <SortLink name="content">content</SortLink>
                </th>
                <th scope="col" style="text-align: left; width: 10rem;">
                    <SortLink name="requestType">requestType</SortLink>
                </th>
                <th scope="col" style="text-align: left; width: 10rem;">
                    <SortLink name="dateTime">date</SortLink>
                </th>
                </tr>
            </thead>
            <tbody slot="body" slot-scope="sort" v-if="!isStatusLoading">
                <tr v-if="isDataLoading">
                    <td><Spinner /></td>
                    <td><Spinner /></td>
                    <td><Spinner /></td>
                    <td><Spinner /></td>
                </tr>
                <tr v-for="value in sort.values" :key="value.id">
                    <td>{{ value.userId }}</td>
                    <td>{{ value.content }}</td>
                    <td>{{ value.requestType }}</td>
                    <td>{{ value.dateTime }}</td>
                </tr>
            </tbody>
        </SortedTable>
        
    </div>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            isStatusLoading: false,
            isDataLoading: false,
            enabled: false,
            secLeft: 5,
            values: [
            ],
            timer: false,
            refreshTimer: false
        }
    },
    mounted() {
        this.fetchLogsStatus(() => {
            this.fetchLogs();
        });
    },
    methods: {
        fetchLogsStatus(callback) {
            let _this = this;
            this.isStatusLoading = true;
            axios.get('http://localhost:8080/logs/status').then(function (res) {
                if(res.data == true)
                    if (callback) callback();
                _this.enabled = res.data;
            }).finally(() => {
                _this.isStatusLoading = false;
            })
        },

        setLogsStatus(val) {
            this.isStatusLoading = true;
            let _this = this;
            axios.post(`http://localhost:8080/logs/status/${val}`).then(function () {
                if (val) {
                    _this.fetchLogs();
                }  else {
                    clearInterval(_this.timer)
                }
                _this.enabled = val;
            }).finally(() => {
                _this.isStatusLoading = false;
            });
        },

        formatDate(dateTime) {
            return `${dateTime.dayOfMonth}/${dateTime.monthValue}/${dateTime.year} ${dateTime.hour}:${dateTime.minute}:${dateTime.second}`;
        },

        fetchLogs() {
            let _this = this;
            this.isDataLoading = true;
            axios.get('http://localhost:8080/logs/').then(function (res) {
                _this.values = res.data.map(log => {
                    console.log(log.dateTime)
                    return {
                        id: log.id,
                        userId: log.user_id,
                        requestType: log.request_type,
                        content: log.content_type,
                        dateTime: _this.formatDate(log.dateTime)
                    }
                });
                _this.startTimerRefresh();
            }).catch(function (err) {
                console.log(err);
            }).finally(() => {
                _this.isDataLoading = false;
            });
        },

        countDown() {
            if (this.secLeft == 1) {
                clearInterval(this.refreshTimer);
                this.fetchLogs();
                this.secLeft = 5;
            }
            else this.secLeft--;
        },

        startTimerRefresh() {
            clearInterval(this.refreshTimer);
            this.refreshTimer = setInterval( this.countDown, 1000);
        }
    },

    watch: {
        enabled(val) {
            if (!val) {
                clearInterval(this.refreshTimer);
            }
        }
    },

    beforeDestroy () {
      clearInterval(this.timer)
    }
}
</script>

