<template>
  <div class="ticketItem">
      <div class="row mainticket">
          <div class="maincol col">
              <div class="row"><div class="line"><span class="icon" id="trainicon"></span></div>
              <div class="col">{{train.number}}</div>
              </div>
              <div class="row">
                <div class="nowrap line bold">
                  {{train.schedules[0].stations[0].mainStation_name}}
                </div>
                  <div class="line"><span class="icon" id="arrowicon"></span></div>
                  <div class="bold nowrap line">
                  {{train.schedules[0].stations[train.schedules[0].stations.length-1].mainStation_name}}
                  </div>
                </div>
          </div>
          <div class="maincol col longer">
              <div class="row nowrap">
                <div class="col center">
                    <div class="row bold">{{getStation(train.from, train.schedules[0].stations).depTime || getStation(train.from, train.schedules[0].stations).arrTime}}</div>
                    <div class="row sfont">{{getStationDay(train.fromDate, train.from, train.schedules[0].stations)}}</div>
                </div>
                <div class="col center">
                    <div class="row bold">
                        {{calculateTimeDifference(train.fromDate, train.from, train.to, train.schedules[0].stations)}}
                    </div>
                    <div class="row sfont">on the way</div>
                </div>
                <div class="col center">
                    <div class="row bold">{{getStation(train.to, train.schedules[0].stations).arrTime || getStation(train.to, train.schedules[0].stations).depTime}}</div>
                    <div class="row sfont">{{getStationDay(train.fromDate, train.to, train.schedules[0].stations)}}</div>
                </div>
              </div>
          </div>
          <div class="maincol col">
              <div class="row nowrap">
                  <div class="col sfont">Platskart</div>
                  <div class="col bold">{{getPrice(train.from, train.to, train.schedules[0].stations, 500)}}</div>
              </div>
              <div class="row nowrap">
                  <div class="col sfont">Kupe</div>
                  <div class="col bold">{{getPrice(train.from, train.to, train.schedules[0].stations, 700)}}</div>
              </div>
          </div>
          <div class="col center">
                <BaseButton class="button" @click="loadSeats()" :loading="loading" :disabled="loading">
                    <div v-if="!show">
                        Choose train
                    </div>
                    <div v-else>
                        Cancel
                    </div>
                </BaseButton>
          </div>
      </div>
    <div v-if="seatsLoaded">
        <SeatComponent
            :trainId="train.id" 
            :fromDate="train.fromDate"
            :from="train.schedules[0].stations.find(station => station.mainStation_name === train.from).id" 
            :to="train.schedules[0].stations.find(station => station.mainStation_name === train.to).id"
            :kupePrice="getPrice(train.from, train.to, train.schedules[0].stations, 700)" 
            :plackartPrice="getPrice(train.from, train.to, train.schedules[0].stations, 500)"
            :carriages="carriages">
        </SeatComponent>
    </div>
  </div>


</template>


<script>
import SeatComponent from './SeatComponent.vue'
import moment from 'moment'
import axios from 'axios'
export default {
    data() {
        return {
            show: false,
            seatsLoaded: false,
            loading: false,
            carriages: false
        }
    },
    components: {
        SeatComponent
    }, 
    props: {
        train: {
            type: Object
        }
    },
    mounted() {
        console.log(this.train.schedules[0].stations.sort((a, b) => {
            const aTime = a.arrTime || a.depTime;
            const bTime = b.depTime || b.arrTime;
            console.log('sorting: ', aTime, bTime)
            if (aTime > bTime) return 1;
            else return -1;
        }));
        console.log(this.train.schedules[0].stations)
    },
    methods: {
        loadSeats() {
            let _this = this;
            this.loading = true
            const date = moment(this.train.fromDate, "DD-MM-YYYY").format("YYYY-MM-DD");
            axios
            .get(`http://localhost:8080/searchTrains/onDate/${date}/depStation/${this.train.from}/arrStation/${this.train.to}/train/${this.train.id}`)
            .then(function(res) {
                console.log(res.data)
                _this.carriages = res.data;
                // _this.trains = res.data.map(train => {return {...train, from: payload.from.name, to: payload.to.name, fromDate: _this.formatDate(payload.date)}});
                // if (res.data.length > 0) {
                // var element = _this.$refs.trains_bottom;
                // element.scrollIntoView({ behavior: 'smooth' });
                // } else {
                // _this.$notify({
                //     group: 'notifications',
                //     title: 'Trains not found',
                //     text: 'There is no train traveling by these routes on given day'
                // });
                // }
            }).catch(function(err) {
                console.log(err);
            }).finally(function() {
                _this.seatsLoaded = true;
                _this.loading = false;
            });
        },
        getStation(from, stations) {
            let station = stations.find(station => station.mainStation_name === from);
            return station;
        },
        getStationDay(fromDate, stationName, stations) {
            let station = stations.find(station => station.mainStation_name === stationName);
            let parsedDate = moment(fromDate, "DD-MM-YYYY").add(-(stations[0].dayNum - 1));
            return moment(parsedDate).add(station.dayNum-1).format("MMMM Do YYYY");
        },
        calculateTimeDifference(fromDate, from, to, stations) {
            let parsedDate = moment(fromDate, "DD-MM-YYYY").add(-(stations[0].dayNum - 1));
            let stationFrom = stations.find(station => station.mainStation_name === from);
            let stationEnd = stations.find(station => station.mainStation_name === to);
            let stationFromDate = moment(parsedDate).add(stationFrom.dayNum-1).format("DD-MM-YYYY");
            let stationEndDate = moment(parsedDate).add(stationEnd.dayNum-1).format("DD-MM-YYYY");
            let stationFromDateTime = moment(`${stationFromDate} ${stationFrom.depTime}`, "DD-MM-YYYY h:mm:ss");
            let stationEndDateTime = moment(`${stationEndDate} ${stationEnd.arrTime}`, "DD-MM-YYYY h:mm:ss");
            let diff = moment.duration(stationFromDateTime.diff(stationEndDateTime));
            return moment.utc(diff.asMilliseconds()).format("H[h] m[min]");
        },
        getPrice(from, to, stations, price) {
            return Math.abs((stations.findIndex(station => station.mainStation_name === to) - stations.findIndex(station => station.mainStation_name === from)) * price);
        }
    }
}
</script>

