<template>
  <div class="modal_container" :class="{ hidden: !isShown }">
    <div class="back_shadow"></div>
    <div class="modal">
      <div class="close" @click="hide()">
        <div class="text">Close</div>
      </div>
      <div class="content">
        <div class="header"></div>
        <div class="body">
            <table class="table">
                <thead>
                    <th>Seat </th>
                    <th>Name</th>
                    <th>Surname</th>
                    <th>Document ID</th>
                    <th>Price</th>
                </thead>
                <tbody>
                    <tr v-for="(seat, index) in selectedSeats" :key="`seat-${seat.id}`">
                        <td>
                            {{seat.number}}, carriage {{seat.carriageNumber}}
                        </td>
                        <td>
                            <input type="text" placeholder="Name" v-model="passengers[index].fName"/>
                        </td>
                        <td>
                            <input type="text" placeholder="Surname" v-model="passengers[index].lName"/>
                        </td>
                        <td>
                            <input placeholder="Document ID" type="number" v-model="passengers[index].document_id"/>
                        </td>
                        <td>
                            {{seat.price}}tg
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="footer">
          <BaseButton
            class="confirm"
            :loading="loading"
            :disabled="loading"
            @click="submit()"
          >
            Book
          </BaseButton>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios';
import { getUserId, getUser } from '../../utils/auth';
import moment from 'moment';
export default {
  props: [
    'selectedSeats', 'fromDate'
  ],
  data() {
    return {
      isShown: false,
      loading: false,
      passengers: [{
                    fName: '',
                    lName: '',
                    document_id: '',
                    seat: '',
                    carriage: '',
                    from: '',
                    to: '',
                    train: '',
                    price: ''
                }],
      postedTicketsCount: 0
    };
  },
  mounted() {
      console.log(this.selectedSeats)
      let _this = this;
      _this.passengers = []
      this.selectedSeats.forEach( seat => {
          _this.passengers.push({
              fName: '',
              lName: '',
              document_id: '',
              seat: seat.id,
              carriage: seat.carriageId,
              from: seat.from,
              to: seat.to,
              train: seat.trainId,
              price: seat.price
          });
      });
  },
  methods: {
    show() {
      this.isShown = true;
    },
    hide() {
      this.isShown = false;
    },
    submit() {
        this.loading = true;
        let _this = this;
        const date = moment(this.fromDate, "DD-MM-YYYY").format("YYYY-MM-DD");
        this.passengers.forEach( (passenger) => {
            Axios.post("http://localhost:8080/tickets/", 
            {
                user: parseInt(getUserId()),
                date,
                ...passenger
            })
            .then(() => {
                _this.ticketAdded()
            })
            .catch(() => {
                _this.$notify({
                    group: 'notifications',
                    title: 'Something went wrong',
                    text: 'Error occured while creating tickets',
                    type: 'warn'
                });
            })
            .finally(() => {
                _this.loading = false;
            });
        })
    },
    ticketAdded() {
        this.postedTicketsCount++;
        if (this.postedTicketsCount == this.passengers.length) {
            this.$notify({
                group: 'notifications',
                title: 'Successfully booked',
                text: 'Tickets have been successully created'
            });
            this.$router.push('/profile');
        }
    }
  },
  watch: {
      selectedSeats() {
            let _this = this;
            _this.passengers = []
            this.selectedSeats.forEach( seat => {
                _this.passengers.push({
                    fName: '',
                    lName: '',
                    document_id: '',
                    seat: seat.id,
                    carriage: seat.carriageId,
                    from: seat.from,
                    to: seat.to,
                    train: seat.trainId,
                    price: seat.price
                });
            });
      }
  }
};
</script>

