<template>
  <div class="settings" v-if="!loading">
    <div class="block">
        <div class="block-name">
            <div class="block-name--title">
                Personal Info
            </div>
            <div class="block-name--info">
            </div>
        </div>
        <div class="block-form">
            <p class="firstRow">Name</p>
            <input type="text" v-model="user.firstName">
            
            <p>Surname</p>
            <input type="text" v-model="user.lastName">
            
            <p>Email</p>
            <input type="text" :value="user.email" disabled>

            <!-- <p>National ID number</p>
            <input type="text"> -->
            <div class="block-name--info" style="color: #7c970f">
                
            </div>
        </div>
    </div>
    <div class="btn_wrapper">
        <BaseButton
            @click="updateInfo()"
            class="form_btn"
            :loading="loadingUserInfo"
            :disabled="loadingUserInfo">
            Save
        </BaseButton>
    </div>
    <div class="block">
        <div class="block-name">
            <div class="block-name--title">
                Password
            </div>
            <div class="block-name--info">
            </div>
        </div>
        <div class="block-form">
            <p class="firstRow">New password</p>
            <input type="text" v-model="password">
        </div>
    </div>
    <div class="btn_wrapper">
        <BaseButton
            @click="updatePassword()"
            class="form_btn"
            :loading="loadingUserPassword"
            :disabled="loadingUserPassword">
            Save
        </BaseButton>
    </div>
  </div>
  <Spinner v-else color="#fff"/>
</template>

<script>
import axios from 'axios';
import { getUser } from '../../utils/auth';
export default {
    data() {
        return {
            loadingUserInfo: false,
            loadingUserPassword: false,
            password: '',
            user: {
                firstName: '',
                lastName: '',
                email: ''
            }
        }
    },
    mounted() {
        let _this = this;
        _this.loadingUserInfo = true;
        _this.loadingUserPassword = true;
        let user = getUser();
        axios.get("http://localhost:8080/users/user_info", {
        auth: {
            username: user.username,
            password: user.password
        }
        }).then(function(res) {
            _this.user = res.data
        }).catch(function() {
            _this.$notify({
                group: "notifications",
                title: "Opps.. Could not sign in",
                text: "Please, check credentials!",
                type: "warn"
            });
        }).finally(function() {
            console.log("finally");
            _this.loadingUserInfo = false;
            _this.loadingUserPassword = false;
        });
    },
    methods: {
        updateInfo() {
            let _this = this;
            _this.loadingUserInfo = true;
            let user = getUser();
            axios.put("http://localhost:8080/users/"+user.user_id, {
                lastName: this.user.lastName,
                firstName: this.user.firstName
            }).then(function(res) {
                _this.user = res.data
            }).catch(function() {
                _this.$notify({
                    group: "notifications",
                    title: "Opps.. Could not update",
                    text: "Errors occured",
                    type: "warn"
                });
            }).finally(function() {
                console.log("finally");
                _this.loadingUserInfo = false;
                _this.$notify({
                    group: "notifications",
                    title: "Successully",
                    text: "Updated",
                });
            });
        },
        updatePassword() {
            let _this = this;
            _this.loadingUserPassword = true;
            let user = getUser();
            axios.put("http://localhost:8080/users/password/"+user.user_id, {
                password: this.password
            }).then(function(res) {
                _this.user = res.data
            }).catch(function() {
                _this.$notify({
                    group: "notifications",
                    title: "Opps.. Could not update",
                    text: "Errors occured",
                    type: "warn"
                });
            }).finally(function() {
                console.log("finally");
                _this.loadingUserPassword = false;
                _this.$notify({
                    group: "notifications",
                    title: "Successully",
                    text: "Updated",
                });
            });
        }
    }
}
</script>

