<template>
<div class="body" v-if="!loading">
    <div class="divTable" style="border: 1px solid #000;" >
        <div class="divTableBody">
            <div class="divTableRow ">
                <div class="divTableCell head">Ticket Number</div>
                <div class="divTableCell head">Passenger Name</div>
                <div class="divTableCell head">Departure Station</div>
                <div class="divTableCell head">Arrival Station</div>
                <div class="divTableCell head">Seat</div>
                <div class="divTableCell head">Carriage</div>
                <div class="divTableCell head">Train</div>
                <div class="divTableCell head">Price</div>
                <div class="divTableCell head">Status</div>
                <div class="divTableCell head">Edit</div>
            </div>
            <div class="divTableRow" v-for="ticket in tickets" :key="ticket.id">
                <div class="divTableCell">{{ticket.id}}</div>
                <div class="divTableCell">{{ticket.lName}} {{ticket.fName}}</div>
                <div class="divTableCell">{{ticket.from_name}}</div>
                <div class="divTableCell">{{ticket.to_name}}</div>
                <div class="divTableCell">{{ticket.seat_number}}</div>
                <div class="divTableCell">{{ticket.carriage_number}}</div>
                <div class="divTableCell">{{ticket.train_number}}</div>
                <div class="divTableCell">{{ticket.price}}</div>
                <div class="divTableCell">{{ticket.status}}</div>
                <div class="divTableCell">
                    <button class="button" id="edit" @click="selectedTicket = JSON.parse(JSON.stringify(ticket)); $refs.modal.show()">Edit</button>
                </div>
            </div>
        </div>  
    </div>
    <Modal ref="modal" @confirm="update()" :loading="loading">
        <div class="form" v-if="selectedTicket">
            <div class="item">
                <div>Passenger Name</div>
                <input type="text" placeholder="Passenger Name" v-model="selectedTicket.fName">
            </div>
            <div class="item">
                <div>Passenger Surname</div>
                <input type="text" placeholder="Passenger Name" v-model="selectedTicket.lName">
            </div>
            <div class="item">
                <div>Document ID</div>
                <input type="text" placeholder="Document ID" v-model="selectedTicket.document_id">
            </div>
            <div class="item">
                <div>Seat Number</div>
                <input type="text" placeholder="Seat Number" v-model="selectedTicket.seat_number">
            </div>
            <div class="item">
                <div>Departure Station ID</div>
                <input type="text" placeholder="departure station id" v-model="selectedTicket.from_id">
            </div>
            <div class="item">
                <div>Arrival Station ID</div>
                <input type="text" placeholder="arrival station id" v-model="selectedTicket.to_id">
            </div>
            <div class="item">
                <div> Train ID</div>
                <input type="text" placeholder="train id" v-model="selectedTicket.train_id">
            </div>
            <div class="item">
                <div>Price</div>
                <input type="text" placeholder="train id" v-model="selectedTicket.price">
            </div>
        </div>
    </Modal>
</div>
<Spinner v-else/>
</template>

<script>
import Axios from 'axios'
export default {
    data() {
      return {
        tickets: [],
        selectedTicket: false,
        loading: false
      }
    },
    mounted() {
      this.fectchList();
    },
    methods: {
      update() {
        this.loading = true;
        let _this = this;
        Axios.put( "http://localhost:8080/tickets/"+this.selectedTicket.id, {
          "price": this.selectedTicket.price,
          "document_id": this.selectedTicket.document_id,
          "carriage": this.selectedTicket.carriage_id,
          "lName": this.selectedTicket.lName,
          "fName": this.selectedTicket.fName,
          "status": this.selectedTicket.status,
          "seat": this.selectedTicket.seat_number,
          "from": this.selectedTicket.from_id,
          "to": this.selectedTicket.to_id,
          "train": this.selectedTicket.train_id
        }).then(() => { 
          _this.selectedTicket = false;
          _this.fectchList();
          _this.$refs.modal.hide();
        }).finally(() => {
          _this.loading = false;
        });
      },
      fectchList() {
        this.loading = true;
        let _this = this;
        this.tickets = [];
        Axios.get("http://localhost:8080/tickets")
        .then((res) => {
          _this.tickets = res.data
        }).catch(() => {
            _this.$notify({
                group: 'notifications',
                title: 'Something went wrong',
                text: 'Error occured while loading tickets',
                type: 'warn'
            });
        })
        .finally(() => {
            _this.loading = false;
        });
      }
    }
    
}
</script>

