<template>
  <div class="travelItem">
      <div class="row mainticket">
          <div class="maincol col">
                <div class="row ">
                      <div class="col sfont nowrap line">Ticket Number </div>
                      <div class="col nowrap line">{{ticket.id}}</div>
                </div>
                <div class="row">
                  <div class="col sfont nowrap line">Passenger </div>
                  <div class="col nowrap line">{{`${ticket.fName} ${ticket.lName}`}}</div>
                </div>
                <div class="row">
                  <div class="col sfont nowrap line">Document</div>
                  <div class="col nowrap line">{{ticket.document_id}}</div>
                </div>
          </div>
          <div class="maincol col">
              <div class="row">
                  <div class="line">
                      <span class="icon" id="trainicon"></span>
                    </div>
                   <div class="col">{{ticket.train_number}}</div> 
                   <!-- <div class="col sfont">{{ticket.trainType}}</div> -->
                </div>
              <div class="row">
                <div class="nowrap line bold">
                  {{ticket.from_name}}
                </div>
                  <div class="line"><span class="icon" id="arrowicon"></span></div>
                  <div class="bold nowrap line">
                  {{ticket.to_name}}
                  </div>
                </div>
          </div>
          <div class="maincol col longer">
              <div class="row nowrap">
                <!-- <div class="col center">
                    <div class="row bold">{{ticket.destTime}}</div>
                    <div class="row sfont">{{ticket.destDay}}</div>
                </div> -->
                <div class="col center">
                    <!-- <div class="row bold">{{ticket.duration}}</div> -->
                    <div class="row bold">{{ticket.ticket_date}}</div>
                </div>
                <!-- <div class="col center">
                    <div class="row bold">{{ticket.arrTime}}</div>
                    <div class="row sfont">{{ticket.arrDay}}</div>
                </div> -->
              </div>
          </div>
          <div class=" col shorter">
              <div class="row nowrap">
                  <div class="col bold">{{ticket.price}}</div>
              </div>
          </div>
          <div class="col center">
                <BaseButton
                    class="button"
                    @click="returnTicket(ticket.id)"
                    v-if="ticket.status == 'booked'"
                    :loading="loading"
                    :disabled="loading">
                  Return the ticket
                </BaseButton>
                <div v-else>
                    returned
                </div>
          </div>
      </div>
  </div>


</template>

<script>
import axios from 'axios'
export default {
    props: ['ticket'],
    data() {
        return {
            loading: false
        }
    },
    methods: {
        returnTicket(id) {
            let _this = this;
            _this.loading = true;
            axios.put("http://localhost:8080/tickets/cancel/"+id).then(function(res) {
                console.log(res)
                _this.ticket.status = "canceled"
            }).catch(function() {
                _this.$notify({
                    group: "notifications",
                    title: "Opps.. Could not sign in",
                    text: "Please, check credentials!",
                    type: "warn"
                });
            }).finally(function() {
            console.log("finally");
            _this.loading = false;
            });
        }
    }
}
</script>

<style lang="scss" scoped>
    .travelItem{
        background: #fff;
        .mainticket{
            padding-top: 10px;
            padding-left: 10px;
            box-shadow: 0 1px 5px rgba(0,0,0,.15);
        }
        .row{
            display: flex;
            flex-direction: row;
            flex-flow: row wrap;
            flex: 1;
            margin-right:5px;
            margin-bottom: 5px; 
        }
        .col{
            display: flex;
            flex-direction: column;
            justify-content: center;
            flex-basis: 100%;
            flex: 1;
            margin-right:5px;
            margin-bottom: 5px; 
        }
        .button{
            width: 120px;
            height: 40px;
            box-shadow: none;
            padding: 7px 12px 8px;
            border-radius: 3px 3px 3px 3px;
            touch-action: manipulation;
            background-color: #fb7b4c;
            border-color: #fb7b4c;
            color: white;
        }
        .longer{
            flex-grow: 2;
            margin-top: 10px;
        }
        .shorter{
            flex-grow: 0.5;
        }
        .nowrap{
            flex-wrap: nowrap;
            overflow: hidden;
            white-space: nowrap;
        }
        .sfont{
            font-size: 0.775em; 
            color: grey;
        }
        .bold{
            font-weight:bold;
        }
        #arrowicon{
            margin-right:3px;
            margin-left: 3px; 
            width: 10px;
            height: 10px;
            display: inline-block;
            background-image: url('../../assets/icons/right-arrow.svg');
        }
        #trainicon{
            margin-right:30px;
            margin-left: 5px; 
            width: 20px;
            height: 20px;
            display: inline-block;
           
            background-image: url('../../assets/icons/train.svg');
        }
        .line{
            display: flex;
            flex-direction: column;
            justify-content: center;
            object-fit: cover;
        }
        .maincol{
            border-right-style:inset; 
            border-right-width: 1.5px;
            margin-right: 20px;
            margin-left: 3px;
        }
        .center{
            align-items: center;
        }
        
    }
</style>