<template>
  <div class="stations" v-if="!loading">
      <table class="table">
          <thead>
              <th>ID</th>
              <th>FirstName</th>
              <th>LastName</th>
              <th>Hired Date</th>
              <th>Wage/hour</th>
              <th>Working from</th>
              <th>Working To</th>
              <th>Quit</th>
              <th></th>
          </thead>
          <tbody>
              <tr v-for="agent in agents" :key="agent.id">
                  <td>{{agent.id}}</td>
                  <td>{{agent.firstName}}</td>
                  <td>{{agent.lastName}}</td>
                  <td>{{agent.hired}}</td>
                  <td>{{agent.wage}}</td>
                  <td>{{agent.from}}</td>
                  <td>{{agent.to}}</td>
                  <td>{{agent.quit || 'still working'}}</td>
                  <td>
                      <BaseButton >
                        EDIT
                      </BaseButton>
                  </td>
              </tr>
          </tbody>
      </table>
    <!-- <Modal ref="modal" @confirm="update()" :loading="loading">
        <div class="form" v-if="selectedStation">
            <div class="item">
                <div>Name</div>
                <input type="text" placeholder="Passenger Name" v-model="selectedStation.name">
            </div>
            <div class="item">
