  </div>
  <Spinner v-else/>
</template>

<script>
import Axios from 'axios';
export default {
    data() {
      return {
        loading: false,
        agents: []
      }
    },
    mounted() {
      this.loading = true;
      let _this = this;
      Axios.get("http://localhost:8080/manager/agents/")
      .then((res) => {
        _this.agents = res.data;
      }).catch(() => {
          _this.$notify({
              group: 'notifications',
              title: 'Something went wrong',
              text: 'Error occured while loading tickets',
              type: 'warn'
          });
      })
      .finally(() => {
          _this.loading = false;
      });
    },
    methods: {
      
    }
}
</script>

