<template>
  <div>
    <Header/>
    <div class="home">
      <div class="search_container">
        <!-- <div class="swithcer">
          <div
            class="tab schedule"
            :class="{'active': currentRoute =='schedules'}"
            @click="$router.push('/schedules')">
            <span>Schedules</span>
          </div>
          <div
            class="tab buy_ticker"
            :class="{'active': currentRoute =='tickets' || currentRoute =='home'}"
            @click="$router.push('/tickets')">
            Buy trains
          </div>
        </div> -->
        <div class="search_form">
          <SearchTicketForm
            @submit="searchTicket"
            :isLoading="isLoading"
            v-if="currentRoute =='tickets' || currentRoute =='home'"
          />
          <ScheduleForm class="under_development" v-else />
        </div>
      </div>
      
      <div class="trains" id="trains" ref="trains" v-if="currentRoute =='tickets' || currentRoute =='home'">
        <TrainItem class="ticket" v-for="train in trains" :train="train" :key="train.id" />
        <!-- <SeatComponent />  -->
        <div class="bottom" ref="trains_bottom"></div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchTicketForm from '../components/SearchTicketForm.vue'
import TrainItem from './Home/TrainItem.vue'
import ScheduleForm from '../components/ScheduleForm.vue'
import axios from 'axios'
export default {
  components: {
    SearchTicketForm,
    TrainItem,
    ScheduleForm
  },
  data() {
    return {
      someData: 'I am some data from component',
      isLoading: false,
      trains: []
    }
  },
  methods: {
    searchTicket(payload) {
      this.isLoading = true;
      let _this = this;
      axios
      .get(`http://localhost:8080/searchTrains/onDate/${this.formatDate(payload.date)}/depStation/${payload.from.name}/arrStation/${payload.to.name}`)
      .then(function(res) {
        _this.trains = res.data.map(train => {return {...train, from: payload.from.name, to: payload.to.name, fromDate: _this.formatDate(payload.date)}});
        if (res.data.length > 0) {
          var element = _this.$refs.trains_bottom;
          element.scrollIntoView({ behavior: 'smooth' });
        } else {
          _this.$notify({
            group: 'notifications',
            title: 'Trains not found',
            text: 'There is no train traveling by these routes on given day'
          });
        }
      }).catch(function(err) {
        console.log(err);
      }).finally(function() {
        _this.isLoading = false;
      });

    },
    formatDate(date) {
        var d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();

        if (month.length < 2) 
            month = '0' + month;
        if (day.length < 2) 
            day = '0' + day;

        return [day, month, year].join('-');
    }
  },
  computed: {
    currentRoute() {
      return this.$route.name
    }
  }
}
</script>

