import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'
import Sign from './views/Sign.vue'
import SignIn from './views/Sign/In.vue'
import SignUp from './views/Sign/Up.vue'
import Profile from './views/Profile.vue'
import Orders from './views/profile/Orders.vue'
import Info from './views/profile/Info.vue'
import Admin from './views/Admin.vue'
import AdminRoutesList from './views/Admin/RoutesList.vue'
import AdminRoutesCreate from './views/Admin/RoutesCreate.vue'
import AdminStations from './views/Admin/Stations.vue'
import AdminTickets from './views/Admin/Tickets.vue'
import AdminEmployees from './views/Admin/Employees.vue'
import AdminLogs from './views/Admin/Logs.vue'
import AdminSchedules from './views/Admin/Schedules.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/tickets',
      name: 'tickets',
      component: Home
    },
    {
      path: '/schedules',
      name: 'schedules',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/sign',
      name: 'sign',
      component: Sign,
      children: [
        {
          path: 'up',
          name: 'signup',
          component: SignUp,
        },
        {
          path: 'in',
          name: 'signin',
          component: SignIn,
        }
      ]
    },
    {
      path: '/profile',
      name: 'profile',
      component: Profile,
      children: [
        {
          path: '',
          name: 'orders',
          component: Orders,
        },
        {
          path: 'orders',
          name: 'orders',
          component: Orders,
        },
        {
          path: 'info',
          name: 'info',
          component: Info,
        }
      ]
    },
    {
      path: '/admin',
      name: 'admin',
      component: Admin,
      children: [
        {
          path: '',
          component: AdminEmployees
        },
        {
          path: 'employees',
          component: AdminEmployees
        },
        {
          path: 'tickets',
          component: AdminTickets
        },
        {
          path: 'routes',
          component: AdminRoutesList
        },
        {
          path: 'routes/create',
          component: AdminRoutesCreate
        },
        {
          path: 'logs',
          component: AdminLogs
        },
        {
          path: 'stations',
          component: AdminStations
        },
        {
          path: 'schedules',
          component: AdminSchedules
        },
      ]
    }
  ]
})
