<template>
  <div v-if="!isTicketsLoading">
    <Ticket v-for="ticket in tickets" :key="ticket.id" :ticket="ticket"/>
  </div>
  <Spinner v-else color="#fff"/>
</template>

<script>
import Ticket from "./OrderedTicket.vue";
import axios from "axios";
import {getUser} from '../../utils/auth';
export default {
  components: {
    Ticket
  },
  data() {
    return {
      isTicketsLoading: true,
      tickets: [],
      stations: []
    };
  },
  mounted() {
    let _this = this;
    _this.loading = true;
    let user = getUser();
    axios.get("http://localhost:8080/users/user_info", {
      auth: {
        username: user.username,
        password: user.password
      }
    }).then(function(res) {
      console.log(res)
      _this.tickets = res.data.ticket
    }).catch(function() {
      _this.$notify({
        group: "notifications",
        title: "Opps.. Could not sign in",
        text: "Please, check credentials!",
        type: "warn"
      });
    }).finally(function() {
      console.log("finally");
      _this.isTicketsLoading = false;
    });
  }
};
</script>

