export function saveUser(id, username, password, roles) {
    localStorage.setItem('username', username);
    localStorage.setItem('user_id', id);
    localStorage.setItem('password', password);
    localStorage.setItem('roles', roles);
}

export function logout() {
    localStorage.removeItem('username')
    localStorage.removeItem('user_id')
    localStorage.removeItem('password')
    localStorage.removeItem('roles')
}

export function getUserId() {
    return localStorage.getItem('user_id')
}

//ROLE_PASSENGER,ROLE_MANAGER,ROLE_EMPLOYEE

export function getUser() {
    return {
        user_id: localStorage.getItem('user_id'),
        username: localStorage.getItem('username'),
        password: localStorage.getItem('password'),
        roles: localStorage.getItem('roles')
    }
}

export function isManager() {
    if (localStorage.getItem('roles')) {
        const roles = localStorage.getItem('roles');
        return roles.indexOf('ROLE_MANAGER') != -1;
    } else {
        return false
    }
}

export function isAgent() {
    if (localStorage.getItem('roles')) {
        const roles = localStorage.getItem('roles');
        return roles.indexOf('ROLE_EMPLOYEE') != -1;
    } else {
        return false
    }
}

export function isPassenger() {
    if (localStorage.getItem('roles')) {
        const roles = localStorage.getItem('roles');
        return roles.indexOf('ROLE_PASSENGER') != -1;
    } else {
        return false
    }
}

export function isSignedIn() {
    if (localStorage.getItem('username'))
        return true
    return false
}