<template>
  <div class="schedules" v-if="!loading">
    <div class="schedule" v-for="schedule in schedules" :key="schedule.id">
      <div class="name">Schedule {{ schedule.id }}</div>
      <table class="table">
        <thead>
          <th>ID</th>
          <th>Name</th>
          <th>Arrival Time</th>
          <th>Departure Time</th>
          <th></th>
        </thead>
        <tbody>
          <tr v-for="station in schedule.stations" :key="station.id">
            <td>{{ station.id }}</td>
            <td>{{ station.mainStation_name }}</td>
            <td>{{ station.arrTime || "not set" }}</td>
            <td>{{ station.depTime || "not set" }}</td>
            <td>
              <BaseButton
                @click="
                  deleteStation(
                    schedule.train_id,
                    schedule.id,
                    station.mainStation_name
                  )
                "
                :loading="deleting"
                :disabled="deleting"
              >
                DELETE
              </BaseButton>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="add_btn_row">
        <BaseButton
            @click="
            newStation.train_id = schedule.train_id;
            newStation.schedule_id = schedule.id;
            $refs.modal.show();
            "
            secondary
        >
            Add new Station
        </BaseButton>
      </div>
      
    </div>

    <Modal ref="modal" @confirm="addStation()" :loading="adding">
      <div class="form">
        <div class="item">
          <div>Name</div>
          <input
            type="text"
            placeholder="Station Name"
            v-model="newStation.name"
          />
        </div>
        <div class="item">
          <div>arrival Time</div>
          <input
            type="text"
            placeholder="Dep Time"
            v-model="newStation.arrTime"
          />
        </div>
        <div class="item">
          <div>departure Time</div>
          <input
            type="text"
            placeholder="Dep Time"
            v-model="newStation.depTime"
          />
        </div>
        <div class="item">
          <div>Day number (number of days after train started traveling)</div>
          <input
            type="text"
            placeholder="Day number "
            v-model="newStation.dayNum"
          />
        </div>
      </div>
    </Modal>
  </div>
  <Spinner v-else />
</template>

<script>
import Axios from "axios";
export default {
  data() {
    return {
      schedules: [],
      loading: false,
      deleting: false,
      adding: false,
      newStation: {
        name: "",
        arrTime: "",
        depTime: "",
        dayNum: "",
        train_id: "",
        schedule_id: ""
      }
    };
  },
  mounted() {
    this.fetchSchedules();
  },

  methods: {
    fetchSchedules() {
      this.loading = true;
      let _this = this;
      _this.schedules = [];
      Axios.get("http://localhost:8080/searchTrains/schedules/")
        .then(res => {
          _this.schedules = res.data;
          _this.schedules.forEach(schedule => {
            schedule.stations = schedule.stations.sort((a, b) => {
              const aTime = a.arrTime || a.depTime;
              const bTime = b.depTime || b.arrTime;
              console.log("sorting: ", aTime, bTime);
              if (aTime > bTime) return 1;
              else return -1;
            });
          });
        })
        .catch(() => {
          _this.$notify({
            group: "notifications",
            title: "Something went wrong",
            text: "Error occured while loading schedules",
            type: "warn"
          });
        })
        .finally(() => {
          _this.loading = false;
        });
    },

    deleteStation(trainId, scheduleId, name) {
      this.deleting = true;
      let _this = this;
      Axios.delete(
        `http://localhost:8080/manager/trains/${trainId}/schedule/${scheduleId}/station/${name}`
      )
        .then(() => {
          _this.$notify({
            group: "notifications",
            title: "Successfully",
            text: "updated"
          });
          _this.fetchSchedules();
        })
        .catch(() => {
          _this.$notify({
            group: "notifications",
            title: "Something went wrong",
            text: "Error occured while deleting schedules",
            type: "warn"
          });
        })
        .finally(() => {
          _this.deleting = false;
        });
    },
    addStation() {
      //http://localhost:8080/manager/trains/1/schedule/1/
      this.adding = true;
      let _this = this;
      Axios.put(
        `http://localhost:8080/manager/trains/${this.newStation.train_id}/schedule/${this.newStation.schedule_id}/`,
        {
          depTime: this.newStation.depTime,
          arrTime: this.newStation.arrTime,
          dayNum: this.newStation.dayNum,
          name: this.newStation.name
        }
      )
        .then(() => {
          _this.$notify({
            group: "notifications",
            title: "Successfully",
            text: "updated"
          });
          _this.fetchSchedules();
          _this.$refs.modal.hide();
        })
        .catch(() => {
          _this.$notify({
            group: "notifications",
            title: "Something went wrong",
            text: "Error occured while deleting schedules",
            type: "warn"
          });
        })
        .finally(() => {
          _this.adding = false;
        });
    }
  }
};
</script>

