  width: 100%;
  padding: 40px 46px;
  .schedule {
    border: 1px solid #fb7b4c;
    margin-top: 24px;
  }
  .table {
    width: 100%;
    th,
    td {
      text-align: left;
    }
  }
}

.add_btn_row {
    width: 100%;
    margin-top: 14px;
    display: flex;
    justify-content: center;
    margin-bottom: 14px;
}
</style>
