<template>
  <div>
    <div data-car-type="1" class="all_cariages">
     <div class="carriage" :class="{active: carriage.carriage_id == selectedCarriageId}" v-for="carriage in carriages" :key="carriage.carriage_id" @click="selectedCarriageId = carriage.carriage_id">
         {{carriage.carriage_number}} carriage ({{carriage.carriage_type}})
     </div>
    </div>
    <div class="overflow-carriage" v-if="selectedCarriageId">
      <div class="carriage">
        <div class="left-side"><span class="carrier-number">{{carriages.find(x => x.carriage_id == selectedCarriageId).carriage_number}}</span></div>
        <div class="center-side">
          <div class="center-side">
            <div class="section" v-for="section in [1, 2, 3, 4, 5]" :key="`section-${section}`">
              <div class="coupe">
                <span   
                    class="place" 
                    v-for="seatNumber in [1, 2, 3, 4]" 
                    :key="`seat-${seatNumber}-${section}`"
                    :class="{
                        'lft-btm': ((section-1)*4+seatNumber) % 4 == 1,
                        'lft-top': ((section-1)*4+seatNumber) % 4 == 2,
                        'rgt-btm': ((section-1)*4+seatNumber) % 4 == 3,
                        'rgt-top': ((section-1)*4+seatNumber) % 4 == 0,
                        'active': selectedSeats[selectedCarriageId] && selectedSeats[selectedCarriageId].indexOf(((section-1)*4+seatNumber)) != -1,
                        'disabled': carriages.find(x => x.carriage_id == selectedCarriageId).seats.findIndex(x=>x.number == (section-1)*4+seatNumber) == -1
                    }"
                    @click="toggleSelected((section-1)*4+seatNumber,selectedCarriageId)"
                    >
                    <span>
                        {{  ((section-1)*4+seatNumber) }}
                    </span>
                </span>
              </div>
            </div>
          </div>
        </div>
        <div class="right-side"></div>
      </div>
    </div>
    <div>
        <div class="data_places">
            <div class="selected def">
            <div class="icon"></div>
            <span>Selected seats</span>
            </div>
            <div class="available def">
            <div class="icon"></div>
            <span>Available seats</span>
            </div>
            <div class="reserved def">
            <div class="icon"></div>
            <span>Reserved seats</span>
            </div>
        </div>
        <div class="selected_seats" v-if="getSelectedSeatsList().length > 0">
            Chosen seats: <span v-for="[seat, carriage] in getSelectedSeatsList()" :key="`selected-${seat}-${carriage}`">{{seat}} (carriage {{carriage}}); </span>
        </div>
        <div class="book_row">
            <BaseButton @click="openModal()" :secondary="getSelectedSeatsForModal().length == 0">
                Proceed to booking
            </BaseButton>
        </div>
    </div>
    <SelectedPassengersModal
        ref="modal"
        v-if="getSelectedSeatsForModal().length > 0"
        :selectedSeats="getSelectedSeatsForModal()"
        :fromDate="fromDate">
    </SelectedPassengersModal>
  </div>
</template>

<script>
import SelectedPassengersModal from './SelectPassengersModal.vue';
import { isSignedIn } from '../../utils/auth';
export default {
    components: {
        SelectedPassengersModal
    },
    props: ['kupePrice', 'plackartPrice', 'trainId', 'from', 'to', 'carriages', 'fromDate'],
  data() {
    return {
      fourth: 0,
      selected: "",
      checkedSeats: [],
      selectedCarriageId: 1,
      selectedSeats: {}
    };
  },
  mounted() {
  },
  methods: {
      openModal() {
          if (isSignedIn()) {
              if (this.getSelectedSeatsForModal().length > 0) this.$refs.modal.show();
          } else {
            this.$notify({
                group: 'notifications',
                title: 'Sign in is required',
                text: 'You should be signed in to book tickets'
            });
          }
      },
    getSeatsOfCoupe(coupe) {
        const carriage = this.carriages.find(x => x.carriage_id === this.selectedCarriageId)
        return carriage.seats.filter(seat => seat.number > (coupe-1)*4 && seat.number < coupe*4+1);
    },

    toggleSelected(seatNumber, carriageNumber) {
        if (this.carriages.find(x => x.carriage_id == carriageNumber).seats.findIndex(x=>x.number == seatNumber) == -1)
            return
        if (this.selectedSeats[carriageNumber]) {
            let index = this.selectedSeats[carriageNumber].indexOf(seatNumber)
            if(index != -1)
                this.selectedSeats[carriageNumber].splice(index, 1);
            else
                this.selectedSeats[carriageNumber].push(seatNumber);
        } else {
            this.$set(this.selectedSeats, carriageNumber, [seatNumber]);
        }
    },
    getSelectedSeatsList() {
        let list = []
        for (let carriage in this.selectedSeats) {
            this.selectedSeats[carriage].forEach(seat => {
                list.push([seat, carriage])
            });
        }
        return list
    },

    getSelectedSeatsForModal() {
        let _this = this
        return this.getSelectedSeatsList().map(([seatNumber, carrNumber]) => {
            let carriage = _this.carriages.find(x => x.carriage_id == carrNumber);
            let seat = carriage.seats.find(x => x.number == seatNumber);
            console.log(carriage, seat);
            return {
                id: seat.id,
                number: seat.number,
                carriageId: carriage.carriage_id,
                carriageNumber: carriage.carriage_number,
                price: carriage.carriage_type == "kupe" ? _this.kupePrice : _this.plackartPrice,
                trainId: _this.trainId,
                from: _this.from,
                to: _this.to
            }
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.all_cariages{
    display: flex;
    flex-wrap: wrap;
    .carriage{
        width: 165px;
        height: 26px;
        padding: 2px 4px;
        background-size: 100%;
        color: #fff;
        margin-left: 4px;
        background: #52aed5;
        margin-top: 8px;
        &.active {
            background: #ff8a00;
        }
        &:hover{
            cursor: pointer;
            background: #ff8a00;
        }
    }
}
.overflow-carriage {
  overflow-x: scroll;
  margin-top: 14px;
  .carriage {
    width: 900px;
    height: 122px;
    display: block;
    background: url(../../assets/carriage_3.png) no-repeat;
    background-size: 100%;
    margin: auto;
    .left-side {
      float: left;
      width: 160px;
      display: block;
      height: 100%;
      position: relative;
      .carrier-number {
        position: absolute;
        left: 30px;
        color: #fff;
        top: 23px;
        width: 30px;
        background-color: #52aed5;
        height: 30px;
        text-align: center;
        line-height: 30px;
      }
    }
    .center-side {
      float: left;
      .section {
        width: 117px;
        float: left;
        .coupe {
          height: 60px;
          position: relative;
          margin: 4px 4px 0 1px;
          .place {
            position: absolute;
            height: 30px;
            line-height: 32px;
            color: #fff;
            background-color: #52aed5;
            width: 25px;
            text-align: center;
            &.active {
              background-color: #ff8a00;
              color: #fff;
            }
            &.disabled{
                background-color: #9ea3b7;
                color: #fff;
                &:hover{
                    cursor: not-allowed !important;
                    background-color: #9ea3b7 !important;
                }
            }
            &:hover{
                cursor: pointer;
                background-color: #ff8a00;
            }
          }
          .lft-btm {
            left: 0;
            bottom: 0;
            border-bottom-right-radius: 3px;
          }
          .lft-top {
            left: 0;
            top: 0;
            border-top-right-radius: 3px;
            border-bottom: 2px solid #fff;
          }
          .rgt-btm {
            right: 0;
            bottom: 0;
            border-bottom-left-radius: 3px;
          }
          .rgt-top {
            right: 0;
            top: 0;
            border-top-left-radius: 3px;
            border-bottom: 2px solid #fff;
          }
        }
      }
    }
    .right-side {
      float: left;
      width: 30px;
      display: block;
      height: 100%;
    }
  }
}

.data_places {
    display: flex;
    justify-content: center;
    .def{
        display: flex;
        align-items: center;
        margin-left: 8px;
        span{
            margin-left: 8px;
        }
        .icon{
            width: 16px;
            height: 16px;
        }
        &.selected{
            .icon{
                background-color: #ff8a00;
            }
        }
        &.available{
            .icon{
                background-color: #52aed5;
            }
        }
        &.reserved{
            .icon{
                background-color: #9ea3b7;
            }
        }
    }
}
.book_row{
    display: flex;
    justify-content: flex-end;
}
</style>
